"use client";
import Image from "next/image";
import { useEffect, useRef } from "react";

export default function FlyingLogo() {
  const logoRef = useRef(null);

  useEffect(() => {
    // Check if the animation is being applied
    const logo = logoRef.current;
    if (logo) {
      const computedStyle = window.getComputedStyle(logo);
      const animationName = computedStyle.getPropertyValue("animation-name");
      if (animationName !== "flyLogo") {
        // If not animating, force reflow and re-apply class
        logo.classList.remove("flying-logo");
        // Force reflow
        void logo.offsetWidth;
        logo.classList.add("flying-logo");
      }
    }
  }, []);

  return (
    <div className="logo-container">
      <Image
        ref={logoRef}
        src="/svg/AVASA.svg"
        alt="Avasa Logo"
        width={100}
        height={100}
        className="flying-logo"
        priority
      />
      <style jsx>{`
        .logo-container {
          position: absolute;
          top: 20%;
          left: -10%;
          width: 100px;
          height: 100px;
          z-index: 1000;
        }

        .flying-logo {
          width: 100%;
          height: 100%;
          animation: flyLogo 5s ease-in-out infinite;
        }

        @keyframes flyLogo {
          0% {
            transform: translateX(0) translateY(0) scale(1) rotate(0deg);
            opacity: 0;
          }
          10% {
            opacity: 1;
          }
          50% {
            transform: translate(80vw, -10vh) scale(1.2) rotate(360deg);
            opacity: 1;
          }
          90% {
            opacity: 1;
          }
          100% {
            transform: translate(100vw, -20vh) scale(0.8) rotate(720deg);
            opacity: 0;
          }
        }
      `}</style>
    </div>
  );
}
